/* Box colors */
.bg-1 {
  background: #eceff1;
  color: #37474f;
}
.bg-2 {
  background: #7986cb;
  color: #eceff1;
}
.bg-3 {
  background: #37474f;
  color: #fff;
}
/* Common button styles */
.button {
  float: left;
  min-width: 150px;
  max-width: 250px;
  display: block;
  padding: 1em 2em;
  border: none;
  background: none;
  color: inherit;
  vertical-align: middle;
  position: relative;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
}
.button:focus {
  outline: none;
}
.button > span {
  vertical-align: middle;
}
/* Text color adjustments (we could stick to the "inherit" but that does not work well in Safari) */
.bg-1 .button {
  color: #37474f;
  border-color: #37474f;
}
.bg-2 .button {
  color: #eceff1;
  border-color: #eceff1;
}
.bg-3 .button {
  color: #fff;
  border-color: #fff;
}
/* Sizes */
.button--size-s {
  font-size: 14px;
}
.button--size-m {
  font-size: 16px;
}
.button--size-l {
  font-size: 18px;
}
/* Typography and Roundedness */
.button--text-upper {
  letter-spacing: 2px;
  text-transform: uppercase;
}
.button--text-thin {
  font-weight: 300;
}
.button--text-medium {
  font-weight: 500;
}
.button--text-thick {
  font-weight: 600;
}
.button--round-s {
  border-radius: 5px;
}
.button--round-m {
  border-radius: 15px;
}
.button--round-l {
  border-radius: 40px;
}
/* Wapasha */
.button.button--wapasha {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#56a3ff), to(#396aff));
  background-image: linear-gradient(to top, #56a3ff, #396aff);
  color: #396aff;
  -webkit-transition: background-color 0.3s, color 0.3s;
  transition: background-color 0.3s, color 0.3s;
  border-radius: 2px;
}
.button--wapasha:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: white;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  z-index: -2;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.button--wapasha::before, .common-botton::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 2px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#56a3ff), to(#396aff));
  background-image: linear-gradient(to top, #56a3ff, #396aff);
  z-index: -1;
  opacity: 0;
  -webkit-transform: scale3d(0.6, 0.6, 1);
  transform: scale3d(0.6, 0.6, 1);
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.button--wapasha.button--inverted::before {
  border-color: #7986cb;
}
.button--wapasha:hover {
  color: white;
}
.button--wapasha.button--inverted:hover {
  background-color: #37474f;
  color: #7986cb;
}
.button--wapasha:hover::before {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  opacity: 1;
}
